<template>
    <div class="ybacerbox">
        <!-- Header -->
        <header class="flex justify-between items-center p-4 bg-blue-600 text-white acerheimg">
            <div class="logoimg">
                <img src="https://www.xdf.cn/zhuanti/bd-phonetic-alphabet-card/resource/img/top-bg-c763567.png"
                    class="top_img" alt="logo">
            </div>
        </header>

        <!-- Content Box -->
        <div class="p-4">
            <!-- 音标学习内容 -->
            <div class="acer-btns card">
                <button @click="acerArr(symbol.symbols)" class=" acer-btns-btns btn btn-info btn-secondary"
                    v-for="(symbol, i) in aceryb" :key="i">{{
                        symbol.name }}</button>
            </div>
            <!-- 动态生成音标选项卡 -->
            <div class="card acer-card-s  bg-base-100 w-95 shadow-xl">
                <div class="card-body">
                    <div class="card-actions justify-end">
                        <button @click="acersymbol(item)" class="acer-fontsize  btn btn-warning"
                            v-for="(item, i) in symbolsItem" :key="i">
                            {{ item.symbol }}
                        </button>
                    </div>
                </div>
            </div>
            <!-- 图解容器 -->
            <div id="diagram-container" class="mt-4 acer-asdiwj" v-show="loadImg">
                <div class="card bg-base-100 image-full w-95 shadow-xl ">
                    <figure :class="!imgacer ? 'imgsizd' : ''">
                        <img :src="imgacer" alt="Shoes" v-show="imgacer" />
                        <span v-show="true" class="loading loading-spinner text-primary"></span>
                    </figure>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
import { ybdata, ybdatamp } from '@/archive/user'

export default {
    name: 'Phonetic',
    data() {
        return {
            isDarkMode: false,
            imgacer: null,
            loadImg: false,
        }
    },
    methods: {
        toggleDarkMode() {
            this.isDarkMode = !this.isDarkMode;
            if (this.isDarkMode) {
                document.body.classList.add('dark');
            } else {
                document.body.classList.remove('dark');
            }
        },
        acerArr(symbols) { 
            this.$store.commit('setSymbolsItem', symbols); // 更新状态
            console.log(this.symbolsItem,'symbolsItem');
            
        },
        async acersymbol(e) {
            this.imgacer = null
            const { publicUrl } = await ybdata(e.key)
            const mp = await ybdatamp(e.key)

            this.loadImg = true

            if (mp) { 
                let audio = new Audio(mp.publicUrl);
                this.imgacer = publicUrl
                audio.play();
            } 
        },
    },
    created() {
        // this.ipaSymbols = data  

    },
    computed: {
        ...mapState({
            aceryb: state => state.aceryb,
            symbolsItem: state => state.symbolsItem,
        })
    }

}
</script>

<style scoped>
.acer-btns {
    display: flex;
    overflow: overlay;
    transform: translateY(-30px);
    background: #ffffff;
    border-radius: 1rem 1rem 0 0;
    flex-direction: row;
}

.logoimg {
    width: 100%;
}

.logoimg>img {
    width: 100%;
}

.imgsizd {
    height: 15rem;
}

.acerheimg {
    padding: 0;
}
.ybacerbox{
    padding-bottom: 3rem;
}

.acer-card-s,
.acer-asdiwj {
    transform: translateY(-30px);
}

.acer-btns>.acer-btns-btns {
    margin: .9rem 1rem;
    height: 2.5rem;
    min-height: 2rem;
    border-radius: 1.5rem;
}

.card-actions {
    display: flex;
    align-items: flex-start;
    gap: 0.5rem;
    flex-wrap: wrap;
    flex-direction: row-reverse;
}

.acer-fontsize {
    font-size: 1.5rem;
}

.card.image-full:before {
    opacity: 0;
}
</style>